<template>
    <div class="set-password">
      <p class="pwd-tips">密码仅可设置为4-20位的数字、英文、英文符号，不能包含空格</p>
      <form action="" @submit.prevent="submit" class="comForm">
        <div class="input-wrapper">
          <input :type="oldShow?'text':'password'" v-model="sendForm.pwd" :class="errorPwd?'active':''"  @focus="focus($event,'pwd')" @blur="blur($event,'pwd')" placeholder="密码">
          <i class="del" v-show="sendForm.pwd" @mousedown.stop="del('pwd')" :style="delImg"></i>
          <span class="error" >{{errorPwd}}</span>
          <span class="see-icon" :class="oldShow ? 'see':'nosee'" @click="oldShow=!oldShow"></span>
        </div>
        <div class="input-wrapper">
          <input :type="sureShow?'text':'password'" v-model="sendForm.chkPwd"  @focus="focus($event)" @blur="blur($event)" placeholder="确认密码">
          <i class="del" v-show="sendForm.chkPwd" @mousedown.stop="del('chkPwd')" :style="delImg"></i>
          <span class="see-icon" :class="sureShow ? 'see':'nosee'" @click="sureShow=!sureShow"></span>
        </div>
        <div class="submit-wrapper">

          <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
          <input type="submit" value="设置密码" class="submit" :class="allValid? 'active':''" :disabled="!allValid" >
        </div>
      </form>
    </div>
</template>

<script>
import { getComFormMixin } from '@/assets/js/mixins/comForm'
import { ValidateUtil } from '@/assets/js/global'
import { pwdErrorTips } from '@/assets/js/errorTips'
export default {
  name: 'TheSetPassword',
  mixins: [getComFormMixin],
  data () {
    return {
      sureShow: false,
      oldShow: false,
      sendForm: {
        pwd: '',
        chkPwd: ''
      }
    }
  },
  methods: {
    submit () {
      // commit('SET_FORGETPWDSTEP')
      if (this.sendForm.pwd !== this.sendForm.chkPwd) {
        this.errorTips = pwdErrorTips.notSame1
        return
      }
      let postData = {
        password: this.sendForm.pwd,
        rePassword: this.sendForm.chkPwd
      }
      this.$api.user.setPassword(postData).then(res => {
        if (res.code === 1) {
          this.$router.push({
            path: '/setPasswordSucceed'
          })
        }
      })
    },
    blur (event, name) {
      this._onBlur(event)
      switch (name) {
        case 'pwd':
          this.errorPwd = ValidateUtil.pwdRegexs(this.sendForm.pwd).tip
          break
        // case 'pwd':
        //   this.errorPwd = ValidateUtil.pwdRegexs(this.pwdLoginForm.pwd)
        //   break
      }
    }
  },
  computed: {
    allValid () {
      return this.sendForm.pwd.length >= 4 && this.sendForm.chkPwd.length >= 4 && !this.errorPwd
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";
  .set-password{
    background: #fff;
    min-height: 100%;
    .pwd-tips{
      background: #F5F7F7;
      font-size: pxToRem(24px);
      color: #A5A8A8;
      height: pxToRem(70px);
      line-height: pxToRem(70px);
      padding-left: pxToRem(34px);
    }
    .comForm{
      .input-wrapper{
        i.del{
          right:pxToRem(134px);
        }
      }
    }
  }

</style>
